<template>
  <view class="search-result">
    <h5>search result</h5>
  </view>
</template>

<script>
import * as Api from '@/api/api';
export default {
  name: 'search-result',
  data() {
    return {
      keywords: '',
      brandSeries: '', //品牌系列
      coBrandedSeries: '', // 联名系列
      page: 1,
      pageNum: 10, // 每页条数
      sort: -1, // 排序规则 1正序 -1倒序
      tag: '' // 标签；项链，耳饰，戒指，手链
    }
  },
  onLoad(options) {
    this.keywords = options.keywords || '';
    this.brandSeries = options.brandSeries || '';
    this.coBrandedSeries = options.coBrandedSeries || '';
    this.tag = options.tag || '';
    this.getSearchResult();
  },
  methods: {
    async getSearchResult() {
      const params = {
        keyword: this.keywords,
        brandSeries: this.brandSeries,
        coBrandedSeries:  this.coBrandedSeries,
        page: this.page,
        pageNum: this.pageNum,
        sort: this.sort,
        tag: this.tag
      };
      const {data: res} = await Api.jewellerySearch(params);
      console.log(res);
    }
  }
}
</script>

